'use client'
import React, {useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { Plus, X, Pencil, Trash2, ChevronDown } from 'lucide-react';
import edit from '../../../public/assets/Edit Button.png';
import trash from '../../../public/assets/myProjects/Trash Icon.png'
import AddToDoModal from './ToDo/AddToDoModal'
import ToDos from './ToDo/ToDos'
type Task = {
  id: number;
  description: string;
  dueDate: string;
  completed: boolean;
};

type Todo = {
  id: number;
  title: string;
  items: Task[];
};

const initialTodos: Todo[] = [
  {
    id: 1,
    title: 'Project Planning',
    items: [
      {
        id: 1,
        description: "Define project scope by clearly outlining the project’s objectives, deliverables, and boundaries.",
        dueDate: '2024-02-12',
        completed: false,
      },
      {
        id: 2,
        description: 'Identify stakeholders by determining who will be impacted by the project and who will be involved.',
        dueDate: '2024-02-12',
        completed: false,
      },
    ],
  },
  {
    id: 2,
    title: 'Design Phase',
    items: [
      {
        id: 1,
        description: "Design the project’s UI/UX by collaborating with designers to create wireframes and mockups.",
        dueDate: '2024-02-15',
        completed: false,
      },
      {
        id: 2,
        description: 'Develop a system architecture to ensure scalability and performance.',
        dueDate: '2024-02-20',
        completed: false,
      },
    ],
  },
];

export default function ToDo() {
  const [todos, setTodos] = useState<Todo[]>(initialTodos);
  const [isModal, setIsModal] = useState(false);
  const [newListName, setNewListName] = useState('');
  const [expandedLists, setExpandedLists] = useState<number[]>([]);
  const [isTodoModal, setTodoModal]= useState(false);
  const [openTodo, setopenTodo]= useState(false);
  const handleCreateList = () => {
    const trimmedName = newListName.trim();
    if (trimmedName) {
      const newList: Todo = {
        id: todos.length + 1,
        title: trimmedName,
        items: [],
      };
      setTodos([...todos, newList]);
      setNewListName('');
      setIsModal(false);
    }
  };

  const toggleExpandList = (id: number) => {
    setExpandedLists((prev) =>
      prev.includes(id) ? prev.filter((listId) => listId !== id) : [...prev, id]
    );
  };

  const toggleTaskCompletion = (todoId: number, taskId: number) => {
    setTodos((prevTodos) =>
      prevTodos.map((todo) => {
        if (todo.id === todoId) {
          return {
            ...todo,
            items: todo.items.map((task) =>
              task.id === taskId ? { ...task, completed: !task.completed } : task
            ),
          };
        }
        return todo;
      })
    );
  };
  const handleClick=()=>{
    setopenTodo(true);
  }
  return (
    <>
    {openTodo ? <ToDos isOpen= {openTodo} onClose= {()=> setopenTodo(false)}/> : <div className="min-h-screen bg-[#F8F7FA]">
      <div className="pl-6 pr-6 pb-0">
        <nav className="flex items-center justify-between mb-6 text-gray-600 text-sm">
          <div className="flex items-center space-x-2">
            <Link href="/main/dashboard" className="hover:text-gray-900">
              Dashboard
            </Link>
            <span className="text-gray-400">›</span>
            <Link href="/main/overview" className="hover:text-gray-900">
              Project Atlas
            </Link>
            <span className="text-gray-400">›</span>
            <span className="text-black">ToDos</span>
          </div>
        </nav>
        <button
          className="flex flex-row justify-center items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg mt-4"
          onClick={() => setIsModal(true)}
        >
          <Plus className="mr-2" /> Create List
        </button>
        {isTodoModal &&  <AddToDoModal  isOpen={isTodoModal} onClose={() => setTodoModal(false)}/>}
        {isModal && (
          <div
            className="bg-white m-2 p-4 rounded-xl"
            role="dialog"
            aria-labelledby="create-list-title"
            aria-modal="true"
          >
            <div className="flex flex-row items-center justify-between">
              <div className="text-lg font-semibold" id="create-list-title">
                Create List
              </div>
              <button
                aria-label="Close modal"
                onClick={() => setIsModal(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                <X className="h-6 w-6" />
              </button>
            </div>
            <div className="mt-4">
              <div className="mb-2 text-sm font-medium text-gray-700">List Name</div>
              <div className="flex flex-row items-center gap-4">
                <input
                  type="text"
                  value={newListName}
                  onChange={(e) => setNewListName(e.target.value)}
                  className="flex-grow border-2 border-gray-300 rounded-lg p-2"
                  placeholder="Enter list name"
                />
                <button
                  className="px-6 py-2 bg-[#5D56BD] text-white rounded-lg hover:bg-[#4b479c]"
                  onClick={handleCreateList}
                >
                  Create
                </button>
              </div>
            </div>
          </div>
        )}
        {todos.length > 0 ? (
          <div>
            {todos.map((todo) => (
              <div key={todo.id} className="bg-white m-2 p-4 rounded-xl">
                <div className='flex'>
                  <div className="flex flex-row items-end text-black font-semibold mb-2">
                    <button
                      onClick={() => toggleExpandList(todo.id)}
                      className={`transition-transform ${
                        expandedLists.includes(todo.id) ? 'rotate-180' : ''
                      }`}
                    >
                      <ChevronDown />
                    </button>
                    {todo.title}
                  </div>
                  <button className='flex flex-row px-6 py-2 bg-[#5D56BD] text-white rounded-lg hover:bg-[#4b479c] ml-auto' onClick= {()=> setTodoModal(true)}>
                    <Plus className=''/> Add Todo
                  </button>
                </div>
                {expandedLists.includes(todo.id) && (
                  <>
                    <div className="mb-4">
                      <h4 className="font-semibold text-gray-700">Pending Tasks</h4>
                      {todo.items.filter((task) => !task.completed).map((task) => (
                        <div
                          key={`${todo.id}-${task.id}`}
                          className="flex items-center gap-4 p-4 border-b last:border-b-0" >
                          <input
                            type="checkbox"
                            className="w-5 h-5 border-2 border-gray-300 rounded cursor-pointer"
                            checked={task.completed}
                            onChange={() => toggleTaskCompletion(todo.id, task.id)}
                          />
                          <div className="flex-grow">
                            <p className="text-sm hover:cursor-pointer" onClick={handleClick}>{task.description}</p>
                            <div className="flex items-center gap-4 mt-2 text-sm text-gray-500">
                              <div>Due date: {task.dueDate}</div>
                            </div>
                          </div>
                          <div className="flex gap-2">
                            <button className="p-1 text-gray-400 hover:text-gray-600">
                              <Image alt="edit" src={edit} width={16} height={16}/>
                            </button>
                            <button className="p-1 text-gray-400 hover:text-gray-600">
                            <Image alt="edit" src={trash} width={16} height={16}/>
                            </button>
                          </div>
                        </div>
                      ))}
                    </div>
                    <div className="mb-4">
                      <h4 className="font-semibold text-gray-700">Completed Tasks</h4>
                      {todo.items.filter((task) => task.completed).map((task) => (
                        <div
                          key={`${todo.id}-${task.id}`}
                          className="flex items-center gap-4 p-4 border-b last:border-b-0"
                        >
                          <input
                            type="checkbox"
                            className="w-5 h-5 border-2 border-gray-300 rounded cursor-pointer"
                            checked={task.completed}
                            onChange={() => toggleTaskCompletion(todo.id, task.id)}
                          />
                          <div className="flex-grow">
                            <p className="text-sm">{task.description}</p>
                            <div className="flex items-center gap-4 mt-2 text-sm text-gray-500">
                              <div>Due date: {task.dueDate}</div>
                              <div>Completed date: {task.dueDate}</div>
                            </div>
                          </div>
                          <div className="flex gap-2">
                          </div>
                        </div>
                      ))}
                    </div>
                  </>
                )}
              </div>
            ))}
          </div>
        ) : (
          <div className="flex flex-col items-center justify-center py-8">
            <Image
              src="/assets/Campfire Illustration.png"
              alt="Empty Status"
              width={400}
              height={400}
            />
            <button
              className="flex flex-row justify-center items-center px-6 py-2 bg-[#5D56BD] text-white rounded-lg"
              onClick={() => setIsModal(true)}
            >
              Create your first ToDo List
            </button>
          </div>
        )}
      </div>
    </div>}
    
    </>
  );
}
