"use client";

import React, { useState, useEffect, use } from "react";
import Link from "next/link";
import Image from "next/image";
import { Plus } from "lucide-react";
import EditProjectModal from "./EditProjectModal";
import { useSearchParams } from "next/navigation";
import axios from "axios";
import {toast} from 'react-toastify'
import { useParams } from "next/navigation";
import AddMilestone from "../dashboard/AddMilestone";
import EditmileStoneModal from "./EditmileStoneModal";
import plusIcon from '../../../public/assets/Plus Icon Manage Team.png'
import AddTeamMembers from "../dashboard/AddTeamMembers";

interface milestoneInterface {
  id: string;
  title: string;
  dueDate: string;
  assignedTo: string;
  completeDate: string;
  status: {
    type: string;
    enum: ["pending", "completed"];
    default: "pending";
  };
}
interface NewTeamMembers{
  _id: string;
  name: string;
  role: string;
}
interface TeamMember {
  _id: string;
  fullName: string;
  role: string;
  position: string;
  avatar: string;
}
interface newMilestoneInterface{
  _id: number;
  description: string;
  assignedTo: string;
  dueDate: string;
}
interface mileStoneThatIsFetchedFromBackend{
  _id: string;
  title: string;
  assignedTo: {
    _id: string;
    name: string;
  };
  dueDate: string;
  status: string;
}
interface Notice {
  id: number;
  title: string;
  content: string;
  Duedate: string;
  author: string;
}

export default function ProjectDashboard() {
  const searchParams = useSearchParams();
  const [milestoneFromBackend, setmilestoneFromBackend]= useState<mileStoneThatIsFetchedFromBackend[]>([])
  const [projectName, setProjectName] = useState<string | null>(null);
  const [milestones, setMilestones] = useState<milestoneInterface[]>([]);
  const [isEdit, setisEdit] = useState(false);
  // Replace with the actual token
  const [token, setToken]= useState<string | null>(null)
  const [error, setError] = useState<string | null>(null);
  const [newMilestone, setNewMilestone]= useState<newMilestoneInterface[]>([])
  const [isAddTeamMemberModalOpen, setisAddTeamMemberModal]= useState<boolean>(false)
  const [isAddMilestone, setisAddMilestone]= useState<boolean>(false);
  const [NewTeamMembers, setNewTeamMembers]= useState<NewTeamMembers[]>([])
  const [tempMembers, setTempMembers]= useState<TeamMember[]>([])
  const [isEditmilestoneModal, setisEditmilestoneModal]= useState<boolean>(false);
    useEffect(()=>{
      const temp= localStorage.getItem("token"); 
      setToken(temp)
    },[token])

  // const [projectData, setProjectData] = useState([]);
  const [teamMembers, setTeamMembers] = useState([
    { _id: "1", fullName: "Naman", position: "Owner", role: "Senior Project Manager", avatar: "api/placeholder" },
  ]);
  const params = useParams();
  const { id } = params;
  const projectId = id;
  const handleAddMember = (newMember: TeamMember) => {
    console.log("hlo world")
      setTempMembers((prev) => {
        const extingIndex = prev.findIndex(
          (member) => member._id === newMember._id
        );
        if (extingIndex !== -1) {
          if (newMember.role === "Select Role") {
            toast.error("Please select a valid role");
          }
          const updatedList = [...prev];
          updatedList[extingIndex] = {
            ...updatedList[extingIndex],
            role: newMember.role,
          };
          return updatedList;
        }
        return [...prev, newMember];
      });
      const payload = tempMembers.map(member => ({
        projectId,
        userId: member._id, // Map _id to userId
        role: member.role?.trim().toLowerCase()
      }));
      const UpdateMembers = async()=>{
              try {
        const response = await axios.post(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/project/add-member`,payload,
          {
            headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
          }
        );
        if (response.status === 200) {
          toast.success("Member added successfully");
          console.log("Project created successfully:", response.data.tempMembers);
        }
      } catch (error) {
        console.error("Failed to create project:", error);
      }
    }
      UpdateMembers()
    };
// First useEffect for fetching
useEffect(() => {
  const fetchUsers = async () => {
      const projectId = id;
      try {
          const response = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/projects/${projectId}/members`, {
              headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
              },
          });
          
          setNewTeamMembers(response.data.members);
          setTempMembers(response.data.members); 
          // console.log(teamMembers)
          // Assuming the data is nested under 'members'
          // console.log("Setting NewTeamMembers to:", response.data.members);
      } catch (error) {
          console.error("Error fetching users:", error);
      }
  };
  fetchUsers();
}, [id]); // Added id as dependency

// Second useEffect for transformation
useEffect(() => {
  
  if (NewTeamMembers && NewTeamMembers.length > 0) {
    // console.log(NewTeamMembers, "newTeamMembers")
      const formattedTeamMembers = NewTeamMembers.map(member => {
          return {
              _id: member._id,
              fullName: member.name,
              position: member.role,
              role: member.role,
              avatar: "api/placeholder"
          };
      });

      // console.log("Setting teamMembers to:", formattedTeamMembers);
      setTeamMembers(formattedTeamMembers);
  }
}, [NewTeamMembers]);

//UseEffect for fetching the upcoming milestones for the side bar
useEffect(() => {
    const projectId= id
  const getMilestone = async()=>{
  try {
    const response = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/projects/${projectId}/milestones`, {
        headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
    });
    const data= response.data
    setmilestoneFromBackend(data.milestones)
} catch (error) {
    console.error("Error fetching users:", error);
}
  }
  getMilestone()
}, []);


  
  const noticeBoard: Notice[] = [
    {
      id: 1,
      title: "Team Meeting Schedule Update",
      content: "Daily standups moved to 10 AM EST",
      Duedate: "2024-01-17",
      author: "Taarush",
    },
  ];

  const milestonesData: milestoneInterface[] = [
    {
      id: "1",
      title:
        "Complete the collection of all project requirements and stakeholder needs.",
      dueDate: "2024-08-20",
      assignedTo: "Taarush",
      completeDate: "2024-01-20", // corrected date format
      status: {
        type: "pending",
        enum: ["pending", "completed"],
        default: "pending",
      },
    },
    {
      id: "2",
      title: "Complete the Hey Page",
      dueDate: "2024-01-21",
      assignedTo: "Sandesh",
      completeDate: "2024-01-20", // corrected date format
      status: {
        type: "pending",
        enum: ["pending", "completed"],
        default: "pending",
      }, // added status
    },
  ];
 
  useEffect(() => {
    setMilestones(milestonesData);
  }, []);
  // }
  const handleClick = () => {
    setisEdit(true);
  };
 
  return (
    <>
    
      <div className="min-h-screen bg-[#F8F7FA]">
        {/* Header Section */}
    
        <div className="pl-6 pr-6 pb-0">
          <nav className="flex items-center justify-between mb-6 text-gray-600 text-sm">
            {/* Left aligned: Breadcrumb navigation */}
            <div className="flex items-center space-x-2">
              <Link href="/main/dashboard" className="hover:text-gray-900">
                Dashboard
              </Link>
              <span className="text-gray-400">›</span>
              <Link href="/projects" className="hover:text-gray-900">
                Project Atlas
              </Link>
              <span className="text-gray-400">›</span>
              <span className="text-black">
                {projectName}
              </span>{" "}
              {/* Display project name */}
            </div>

            {/* Right aligned: Buttons */}
            <div className="flex items-center space-x-2">
              <button className="p-2 hover:bg-gray-100 rounded-lg border-2">
                <Image src="/assets/Pin.png" alt="Pin" width={24} height={24} />
              </button>
              <button className="p-2 hover:bg-gray-100 rounded-lg border-2">
                <Image
                  src="/assets/Archive Icon.png"
                  alt="Archive"
                  width={24}
                  height={24}
                />
              </button>
              <button
                className="px-4 py-2 bg-[#5D56BD] text-white rounded-lg"
                onClick={handleClick}
              >
                Edit project
              </button>
            </div>
          </nav>
        </div>

        {/* Main Content Grid */}
        <div className="grid grid-cols-3 gap-6 p-6">
          {/* Campfire Section */}
          <div className="col-span-2 bg-white rounded-xl p-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-semibold">Campfire</h2>
              <button className="text-indigo-600 text-sm">Show all</button>
            </div>
            <div className="flex flex-col items-center justify-center py-12">
              <Image
                src="/assets/Campfire Illustration.png"
                alt="No Messages"
                width={120}
                height={120}
              />
              <p className="text-gray-500 mt-4">No Messages</p>
            </div>
          </div>

          {/* Project Status Section */}
          <div className="col-span-1 bg-[#1C1C38] text-white rounded-xl p-6">
            <h2 className="text-xl font-semibold mb-4">Project Status</h2>
            <div className="flex justify-between items-center mb-6">
              <span>On Track</span>
              <button className="px-3 py-1 bg-green-500 rounded-lg text-sm">
                {/* onClick */}
                Update Status
              </button>
            </div>

            {/* Existing Milestones Section */}
            <div className="mb-6">
              {milestones.length > 0 ? (
                milestones.map((milestone) => (
                  <div
                    key={milestone.id}
                    className="bg-white text-[#4D4D4D] p-4 mb-3 rounded-lg"
                  >
                    <div className="flex items-center mb-2">
                      <Image
                        src="/assets/avtar.jpg"
                        alt="avatar"
                        width={34}
                        height={34}
                        className="w-10 h-10 bg-gray-200 rounded-full"
                      />
                      <h3 className="font-medium ml-2">
                        {milestone.assignedTo}
                      </h3>
                    </div>
                    <div className="text-sm">{milestone.title}</div>
                    <div className="text-xs text-gray-500">
                      Due Date: {milestone.dueDate}
                    </div>
                    <div className="text-xs text-gray-500">
                      {/* Completed Date: {milestone.completeDate} */}
                    </div>
                  </div>
                ))
              ) : (
                <div className="flex flex-col items-center justify-center py-8">
                  <Image
                    src="/assets/Empty Project Illustration.png"
                    alt="Empty Status"
                    width={100}
                    height={100}
                  />
                  <p className="text-gray-400 mt-4">Empty Project Status</p>
                </div>
              )}
            </div>

            {/* New Timeline Section */}
            <div className="mt-8">
              <div className="flex items-center mb-4">
                <div className="flex items-center">
                  <svg
                    className="w-5 h-5 mr-2"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path
                      fillRule="evenodd"
                      d="M6 2a1 1 0 00-1 1v1H4a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V6a2 2 0 00-2-2h-1V3a1 1 0 10-2 0v1H7V3a1 1 0 00-1-1zm0 5a1 1 0 000 2h8a1 1 0 100-2H6z"
                      clipRule="evenodd"
                    />
                  </svg>
                  <span className="font-medium">Due Date</span>
                </div>
                <div>
                  <span className="ml-2 text-sm text-gray-300">
                    {new Date().toLocaleDateString("en-GB", {
                      day: "2-digit",
                      month: "short",
                      year: "numeric",
                    })}
                  </span>
                </div>
              </div>
              <div className="relative">
                {milestones.map((task, index) => (
                  <div key={index} className="mb-6 relative pl-8">
                    {/* Timeline connector line */}
                    <div className="absolute left-3 top-0 h-full w-px bg-gray-600" />

                    {/* Timeline number indicator */}
                    <div className="absolute left-0 w-6 h-6 rounded-full bg-[#FFB13B] flex items-center justify-center text-sm">
                      {index + 1}
                    </div>
                    {/* Task content */}
                    <div className="pl-4">
                      <div className="flex items-center mb-2">
                        <Image
                          src="/assets/avtar.jpg"
                          alt="hlo"
                          width={24}
                          height={24}
                          className="rounded-full"
                        />
                        <span className="ml-2 text-sm">{task.assignedTo}</span>
                      </div>
                      <p className="text-sm">{task.title}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Notice Board Section */}
          <div className="col-span-2 bg-white rounded-xl p-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-semibold">Notice Board</h2>
              <button className="text-[#5D56BD] text-sm">Show all</button>
            </div>

            {noticeBoard.length > 0 ? (
              <div className="space-y-4">
                {noticeBoard.map((notice) => (
                  <div
                    key={notice.id}
                    className="border rounded-lg p-4 hover:shadow-md transition-shadow"
                  >
                    <h3 className="font-medium">{notice.title}</h3>
                    <p className="text-gray-600 text-sm mb-3">
                      {notice.content}
                    </p>
                    <div className="flex items-center text-sm text-gray-500">
                      <Image
                        src="/assets/avtar.jpg"
                        alt="avatar"
                        width={34}
                        height={34}
                        className="w-10 h-10 bg-gray-200 rounded-full"
                      />
                      <span className="ml-2">{notice.author}</span>
                      <span className="ml-2">Published on:&nbsp;</span>
                      <span>
                        {new Date(notice.Duedate).toLocaleDateString("en-GB", {
                          day: "2-digit",
                          month: "short",
                          year: "numeric",
                        })}
                      </span>
                      <span className="mx-2"></span>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center py-12">
                <Image
                  src="/assets/Notice Board Illustration.png"
                  alt="Empty Notice Board"
                  width={120}
                  height={120}
                />
                <p className="text-gray-500 mt-4">Empty Notice Board</p>
              </div>
            )}
          </div>

          {/* Team Members Section */}
          <div className="col-span-2 bg-white rounded-xl p-6">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-xl font-semibold">
                Team Members <span>({teamMembers.length})</span>
              </h2>
            </div>
            <div className="grid grid-cols-5 gap-4">
              {/* Team Members Section - Take 4 out of the 5 columns */}
              <div className="grid grid-cols-1 gap-4 col-span-4">
                <div className="grid grid-cols-3 gap-4"> {/* Changed grid-cols-4 to grid-cols-3 */}
                  {teamMembers.slice(0, 5).map((member, index) => (
                    <div
                      key={index}
                      className="flex items-center space-x-4 p-3 bg-[#FAFAFA]"
                    >
                      <Image
                        src="/assets/avtar.jpg"
                        alt="avtar"
                        width={34}
                        height={34}
                        className="w-10 h-10 bg-gray-200 rounded-full"
                      />
                      <div className="text-md text-[#333333]">
                        <p className="font-medium">{member.fullName}</p>
                        <p className="text-[#4D4D4D] text-sm">{member.position}</p>
                      </div>
                    </div>
                  ))}
                  <div className="flex items-center space-x-4 p-3 bg-[#FAFAFA] justify-center text-[#333333]">
                    <div className="text-2xl">+
                      {teamMembers.length>=5 ? +teamMembers.length-5: +0}</div>
                  </div>
                </div>
              </div>

              {/* Button Section - Take the remaining 1 column and take full height and width */}
              <div className="flex items-center justify-center col-span-1 h-full w-full">
                <button className="flex flex-col justify-center items-center px-2 py-2 bg-[#5D56BD] text-white rounded-lg w-full h-full" onClick={()=> setisAddTeamMemberModal(true)}>
                  <div className="-mt-2">
                  <Image src={plusIcon} alt="+" width={48} height={48} className="pt-1.5"/>
                  </div>
                  Manage Team
                </button>
              </div>
            </div>
          </div>
          {/* Milestones Section */}
          <div className="col-span-2 bg-white rounded-xl p-6">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-xl font-semibold">
                Milestones {milestoneFromBackend.length}
              </h2>
              <button className="px-4 py-2 bg-[#5D56BD] text-white rounded-lg flex items-center" onClick={()=> setisAddMilestone(true)}>
                <Plus className="w-4 h-4 mr-2" />
                Add Milestone
              </button>
            </div>
            
            {milestoneFromBackend.length > 0 ? (
              <div className="space-y-4">
                {milestoneFromBackend.map((milestone, index) => (
                  <div
                    key={index}
                    className="flex items-center justify-between bg-gray-50 p-4 rounded-lg hover:shadow-md transition-shadow"
                  >
                    <div className="flex items-center space-x-3">
                      <input
                        type="checkbox"
                        className="w-5 h-5 text-[#5D56BD] rounded"
                        checked={milestone.status === "completed"} // Reflect current status
                        onChange={() => {
                          milestone._id,
                            // milestone.projectId, // Replace with actual projectId if available
                            milestone.status=== "completed"
                              ? "pending"
                              : "completed";
                        }}
                      />
                      <div>
                        <h3 className="font-medium text-gray-900">
                          {milestone.title}
                        </h3>
                        <div className="flex items-center space-x-2 text-sm text-gray-500 mt-1">
                          <Image
                            src="/assets/avtar.jpg"
                            alt="avtar"
                            width={34}
                            height={34}
                            className="w-10 h-10 bg-gray-200 rounded-full"
                          />
                          <span>{milestone.assignedTo._id}</span>
                        </div>
                      </div>
                    </div>
                    <div className="flex items-center space-x-3">
                      <span className="text-sm text-gray-500">
                        Due date:&nbsp;
                        {new Date(milestone.dueDate).toLocaleDateString(
                          "en-GB",
                          {
                            day: "2-digit",
                            month: "short",
                            year: "numeric",
                          }
                        )}
                      </span>
                      <button className="p-2  rounded-lg">
                        <Image
                          src="/assets/Edit Button.png"
                          alt="Edit"
                          width={16}
                          height={16}
                          onClick={() => setisEditmilestoneModal(true)}
                        />
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center py-12">
                <Image
                  src="/assets/Milestone Illustration.png"
                  alt="No Milestone"
                  width={120}
                  height={120}
                />
                <p className="text-gray-500 mt-4">No Milestones</p>
              </div>
            )}
          </div>
        </div>
      </div>
      {isEdit && (
        <EditProjectModal isOpen={isEdit} onClose={() => setisEdit(false)} />
      )}
      {isEditmilestoneModal && (
        <EditmileStoneModal onClose={() => setisEditmilestoneModal(false)} />
      )}
       {isAddMilestone && (
          <AddMilestone
            onClose={() => setisAddMilestone(false)}
            teamMembers={teamMembers}
            setmileStone={setNewMilestone}
            fromOverview={true}
          />
        )}
        {isAddTeamMemberModalOpen && (
          <AddTeamMembers
            onClose={() => setisAddTeamMemberModal(false)}
            onAddMember={handleAddMember}
            teamMembers={teamMembers}
          />
        )}
    </>
  );
}
